import CryptoJS from "crypto-js";
import fs from "fs";

name(process.argv);

function name(argv) {
    let gn, url, key, user_id, user_name, device_id, regtime, days;
    const argv_len = argv.length;

    if (argv_len < 3) {
        console.error("请输入参数运行!");
    } else {
        gn = argv[2];
        switch (gn) {
            case 'enc':
                url = argv[3];
                key = argv[4];
                enc(url, key);
                break;
            case 'dec':
                url = argv[3];
                key = argv[4];
                dec(url, key);
                break;
            case 'addtime':
                url = argv[3];
                key = argv[4];
                device_id = argv[5];
                days = parseInt(argv[6]);
                addtime(url, key, device_id, days);
                break;
            case 'adduser':
                url = argv[3];
                key = argv[4];
                user_id = argv[5];
                user_name = argv[6];
                device_id = argv[7];
                regtime = parseInt(argv[8]) || Date.now();
                adduser(url, key, user_id, user_name, device_id, regtime);
                break;
            case '?':
            case '-help':
                help();
                break;
            default:
                console.error('尚未定义的参数!');
        }
    }
}

function help() {
    const log = `enc(url, key)                      // 加密用户数据,通过RC4加密用户数据
    // url:                        // 待解密/加密文件路径
    // key:                            // 密���
dec(url, key)                      // 解密用户数据,通过RC4解密用户数据
    // url:                        // 待解密/加密文件路径
    // key:                            // 密钥
addtime(url, key, device_id, days) //添加用户时长，单位为天
    // url:                        //待解密文件(加密用户数据)路径
    // key:                            // 密钥
    // device_id:                      // 设备ID
    // days:                           // 增加天数(如已经到期，增加时间为当前时间+天数)
adduser(url, key, user_id, user_name, device_id, regtime)
    // url:                        // 待解密文件(加密用户数据)路径
    // key:                            // 密钥
    // user_id:                        // 用户ID
    // user_name:                      // 用户名
    // device_id:                      // 设备ID
    // regtime:                        // 注册时间13位时间戳, 为空时默认为当前服务器时间`;
    console.log(log);
}

function dec(url, key) {
    if (!fs.existsSync(url)) {
        console.log("文件不存在! 自动空白加密文件");
        fs.writeFileSync(url, "[]");
        enc(url, key);
    }

    const userdata_enc = fs.readFileSync(url, "utf-8");
    const userdata_dec = CryptoJS.RC4.decrypt(userdata_enc.toString(), key).toString(CryptoJS.enc.Utf8);
    fs.writeFileSync(url, userdata_dec);
    let userdata = JSON.parse(userdata_dec);
    return userdata;
}

function enc(url, key) {
    if (!fs.existsSync(url)) {
        console.log("文件不存在! 自动空白加密文件");
        fs.writeFileSync(url, "[]");
    }

    const userdata_dec = fs.readFileSync(url, "utf-8");
    const userdata_enc = CryptoJS.RC4.encrypt(userdata_dec, key).toString();
    fs.writeFileSync(url, userdata_enc);
    return userdata_enc;
}

function addtime(url, key, device_id, days) {
    const userdata = dec(url, key);
    userdata.forEach(element => {
        if (device_id === "*" || element.device_id === device_id) {
            if (element.expiretime <= Date.now()) {
                element.expiretime = Date.now() + parseInt(days) * 24 * 60 * 60 * 1000;
                console.log("超时添加");
            } else {
                element.expiretime += parseInt(days) * 24 * 60 * 60 * 1000;
                console.log("时间累加");
            }
        }
    });

    const userdata_dec = JSON.stringify(userdata);
    fs.writeFileSync(url, userdata_dec);
    const userdata_enc = enc(url, key);
    return userdata_enc;
}

function adduser(url, key, user_id, user_name, device_id, regtime) {
    let userdata = dec(url, key);
    let newuser = {
        user_id,
        user_name,
        device_id,
        regtime,
        expiretime: Date.now(),
    };

    if (userdata.some(element => element.device_id === newuser.device_id)) {
        console.error("设备已存在请勿重复添加！");
    } else {
        userdata.push(newuser);
        let userdata_dec = JSON.stringify(userdata);
        fs.writeFileSync(url, userdata_dec);
    }

    return enc(url, key);
}

